import React, { Component, ComponentClass, ComponentType, FunctionComponent, useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { TwitchEmbed } from 'react-twitch-embed';
import TwitchSvg from '@assets/svg/twitch.svg'
import '@components/Twitch/index.scss';
import WhiteLoader from '@components/Loader/WhiteLoader';
const EMBED_URL = 'https://embed.twitch.tv/embed/v1.js';
const Twitch: FunctionComponent = (props) => {
    const [loaded, setLoaded] = useState<boolean>(false);
    useEffect(() => {
        const twitchElement = document.querySelector('script[src="' + EMBED_URL + '"]');
        if (twitchElement) {
            twitchElement.remove()
        }
        const script = document.createElement('script');
        script.setAttribute(
            'src',
            EMBED_URL
        );
        script.addEventListener('load', () => {
            setLoaded(true);
        });
        document.body.appendChild(script);
    }, [])
    if (!loaded) {
        return <div className="container-fluid twitch-container">
            <div className="container">
                <div className="row">
                    <div className="twitch-logo" style={{ position: "absolute", top: 50 }}>
                        <img
                            src={TwitchSvg}
                            width="150px"
                            height="auto"
                        />
                    </div>
                    <WhiteLoader loaded={loaded} />
                </div>
            </div>
        </div>
    }
    return (
        <div className="container-fluid twitch-container">
            <div className="container">
                <div className="row">
                    <div className="twitch-logo">
                        <img
                            src={TwitchSvg}
                            width="150px"
                            height="auto"
                        />
                    </div>
                    <div className="d-flex align-items-center justify-content-center">
                        <div className="padder" style={{ background: "white" }}>
                            <div>
                                <div>
                                    <div className="justify-content-center">
                                        <TwitchEmbed
                                            channel="cangokceaslan"
                                            id="cangokceaslan"
                                            theme="dark"
                                            allowfullscreen={true}
                                            width="100%"
                                            onVideoPause={() => console.log(':(')}
                                        />
                                    </div>
                                </div>                                </div>
                        </div>
                    </div>
                </div>
            </div>

        </div>
    )
}


export default Twitch;